/* eslint-disable import/no-cycle */
/* eslint-disable import/prefer-default-export */
/* eslint-disable no-param-reassign */
import AVRec from './AVRec';
import popup from './Popup';
import Message from './Message';
import Clip from './Clip';
import api from './ApiConnector';
import pinMsg from './PinMessage';
import './emoji';

function load(document) {
  if (!document) { // –†–∞–Ω–Ω–∏–π –≤—ã—Ö–æ–¥
    return;
  }

  const messagesList = document.getElementById('messages-list'); // —Å–ø–∏—Å–æ–∫ —Å–æ–æ–±—â–µ–Ω–∏–π
  const input = document.getElementById('input'); // input –¥–ª—è –≤–≤–æ–¥–∞ —Å–æ–æ–±—â–µ–Ω–∏—è
  const btnClip = document.getElementById('clip-btn'); // –ö–Ω–æ–ø–∫–∞ –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ —Ñ–∞–π–ª–æ–≤.
  const btnRecAudio = document.getElementById('audio-start-btn'); // –ö–Ω–æ–ø–∫–∞ –∑–∞–ø–∏—Å–∏ –∞—É–¥–∏–æ —Å–æ–æ–±—â–µ–Ω–∏—è
  const btnRecVideo = document.getElementById('video-start-btn'); // –ö–Ω–æ–ø–∫–∞ –∑–∞–ø–∏—Å–∏ –∞—É–¥–∏–æ —Å–æ–æ–±—â–µ–Ω–∏—è
  const startRec = document.getElementById('buttons-start-rec'); // –ë–ª–æ–∫ —Å –∫–Ω–æ–ø–∫–∞–º–∏ –∑–∞–ø–∏—Å–∏ –∞—É–¥–∏–æ/–≤–∏–¥–µ–æ
  const stoptRec = document.getElementById('buttons-stop-rec'); // –ë–ª–æ–∫ —Å –∫–Ω–æ–ø–∫–∞–º–∏ –æ—Å—Ç–∞–Ω–æ–≤–∫–∏ –∑–∞–ø–∏—Å–∏ –∞—É–¥–∏–æ/–≤–∏–¥–µ–æ
  const timer = document.getElementById('timer'); // –¢–∞–π–º–µ—Ä (–¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å –≤–∏–¥–µ–æ/–∞—É–¥–∏–æ –∑–∞–ø–∏—Å–∏)
  const btnOk = document.getElementById('ok-btn'); // –û—Å—Ç–∞–Ω–æ–≤–∫–∞ –≤–∏–¥–µ/–∞—É–¥–∏–æ –∑–∞–ø–∏—Å–∏. –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ
  const btnCancel = document.getElementById('cancel-btn'); // –û—Å—Ç–∞–Ω–æ–≤–∫–∞ –≤–∏–¥–µ/–∞—É–¥–∏–æ –∑–∞–ø–∏—Å–∏. –û—Ç–º–µ–Ω–∞

  api.getPinMsg(pinMsg); // –ó–∞–≥—Ä—É–∂–∞–µ–º pin-—Å–æ–æ–±—â–µ–Ω–∏–µ (–µ—Å–ª–∏ –æ–Ω–æ –µ—Å—Ç—å).
  api.getMessages(messagesList); // –ó–∞–≥—Ä—É–∂–∞–µ–º –ø–æ—Å–ª–µ–¥–Ω–∏–µ 10 —Å–æ–æ–±—â–µ–Ω–∏–π
  const avRec = new AVRec(popup, timer);
  const clip = new Clip(messagesList, async (file, typeFile) => {
    const msg = await Message.createMessage('file', file, typeFile);
    await api.pushMessage(msg);
    await messagesList.append(msg.html);
    messagesList.scrollTop = messagesList.scrollHeight; // —Å–∫—Ä–æ–ª –≤–Ω–∏–∑
  });

  messagesList.addEventListener('scroll', () => {
    if (messagesList.scrollTop === 0) {
      const { id } = messagesList.firstChild.dataset;
      api.getMessages(messagesList, id); // –ó–∞–≥—Ä—É–∂–∞–µ–º –ø–æ—Ä—Ü–∏—é —Å–æ–æ–±—â–µ–Ω–∏–π.
    }
  });

  // –ó–∞–ø—É—Å–∫ –∑–∞–ø–∏—Å—å –ê—É–¥–∏–æ
  btnRecAudio.addEventListener('click', async () => {
    startRec.classList.add('hidden');
    await avRec.startRecordAudio();
    stoptRec.classList.remove('hidden');
  });

  // –ó–∞–ø—É—Å–∫ –∑–∞–ø–∏—Å—å –í–∏–¥–µ–æ
  btnRecVideo.addEventListener('click', async () => {
    startRec.classList.add('hidden');
    await avRec.startRecordVideo();
    stoptRec.classList.remove('hidden');
  });

  // –ó–∞–≤–µ—Ä—à–µ–Ω–∏–µ –∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –≤–∏–¥–µ–æ/–∞—É–¥–∏–æ —Å–æ–æ–±—â–µ–Ω–∏—è
  btnOk.addEventListener('click', () => {
    startRec.classList.remove('hidden');
    stoptRec.classList.add('hidden');
    avRec.recordOk(async () => {
      const msg = await Message.createMessage(avRec.recordType, avRec.record);
      avRec.clear();
      await api.pushMessage(msg);
      messagesList.append(msg.html);
      messagesList.scrollTop = messagesList.scrollHeight; // —Å–∫—Ä–æ–ª –≤–Ω–∏–∑
    });
  });

  // –û—Ç–º–µ–Ω–∞ –∑–∞–ø–∏—Å–∏ –∞—É–¥–∏–æ/–≤–∏–¥–µ–æ —Å–æ–æ–±—â–µ–Ω–∏—è
  btnCancel.addEventListener('click', () => {
    startRec.classList.remove('hidden');
    stoptRec.classList.add('hidden');
    avRec.recordCancel();
  });

  // –û—Ç–ø—Ä–∞–≤—É–≤ —Ç–µ–∫—Å—Ç–æ–≤–æ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏—è
  input.addEventListener('keydown', async (event) => {
    if (event.code !== 'Enter') {
      return;
    }
    if (!input.value) {
      return;
    }
    const msg = await Message.createMessage('text', event.target.value);
    await api.pushMessage(msg);
    messagesList.append(msg.html);
    messagesList.scrollTop = messagesList.scrollHeight; // —Å–∫—Ä–æ–ª –≤–Ω–∏–∑
    event.target.value = null;
  });

  btnClip.addEventListener('click', () => {
    clip.openWindowsChangeFile();
  });
}

load(document);
