/* eslint-disable import/no-cycle */
import api from './ApiConnector';

class PinMessage {
  constructor(pinArea, messagesList) {
    this.id = undefined;
    this.html = undefined;
    this.pinArea = pinArea; // –ó–æ–Ω–∞ –≥–¥–µ –∑–∞–∫—Ä–µ–ø–ª—è–µ—Ç—Å—è —Å–æ–æ–±—â–µ–Ω–∏–µ.
    this.messagesList = messagesList; // –°–ø–∏—Å–æ–∫ –≤—ã–≤–µ–¥–µ–Ω–Ω—ã—Ö —Å–æ–æ–±—â–µ–Ω–∏–π.
  }

  pinned(msg, sendServer = true) { // –ó–∞–∫—Ä–µ–ø–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ
    if (this.id) {
      return false;
    }
    this.id = msg.id;
    this.html = (msg.html).cloneNode(true);
    this.html.classList.add('pin');
    this.html.classList.remove('hidden');
    this.html.getElementsByClassName('dell-btn')[0].remove();
    const pinBtn = this.html.getElementsByClassName('pin-btn')[0];
    pinBtn.classList.remove('hidden');
    pinBtn.onclick = this.unPinned.bind(this);
    if (sendServer) { // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –¥–∞–Ω–Ω—ã–µ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
      api.pinnedMessage(this.id);
    }
    this.pinArea.append(this.html);
    return true;
  }

  unPinned() { // –û—Ç–∫—Ä–µ–ø–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ
    api.unPinnedMessage(); // –£–¥–∞–ª—è–µ–º –¥–∞–Ω–Ω—ã–µ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
    const serchMsg = this.messagesList.querySelector(`[data-id="${this.id}"]`);
    if (serchMsg) {
      serchMsg.classList.remove('hidden');
    }

    this.id = undefined;
    this.html = undefined;
    this.pinArea.innerHTML = '';
  }
}

const pinArea = document.getElementById('messages-pin'); // –ó–æ–Ω–∞ –≥–¥–µ –∑–∞–∫—Ä–µ–ø–ª—è–µ—Ç—Å—è —Å–æ–æ–±—â–µ–Ω–∏–µ.
const messagesList = document.getElementById('messages-list'); // –°–ø–∏—Å–æ–∫ –≤—ã–≤–µ–¥–µ–Ω–Ω—ã—Ö —Å–æ–æ–±—â–µ–Ω–∏–π.

const pinMsg = new PinMessage(pinArea, messagesList);

export default pinMsg;
