/* eslint-disable no-unused-vars */

export default class GeoLocation {
  constructor(popup) {
    this.popup = popup;
    // this.status = '';
    // this.err = '';
    this.coords = undefined;
    this.corrdsText = null;
    this.address = null;
  }

  getGeo() {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        // this.err = '–ì–µ–æ–ª–æ–∫–∞—Ü–∏—è –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è –≤ –í–∞—à–µ–º –±—Ä–∞—É–∑–µ—Ä–µ :(';
        // this.status = false;
        // reject();
        this.address = '–ù–µ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–æ';
        resolve();
      } else {
        navigator.geolocation.getCurrentPosition(async (position) => {
          this.coords = position.coords;
          this.coordsText = `[${this.coords.latitude}, ${this.coords.longitude}]`;
          await this.getPlace();
          this.status = true;
          resolve();
        }, () => {
          // this.err = '–ù–µ–≤–æ–∑–º–æ–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å –≤–∞—à–µ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ :(';
          // this.status = false;
          // reject();
          this.address = '–ù–µ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–æ';
          resolve();
        });
      }
    });
  }

  async getPlace() {
    const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=json&lat=${this.coords.latitude}&lon=${this.coords.longitude}&zoom=18&addressdetails=1`);
    const { address } = await response.json();
    const { country, city } = address;
    if ((country) && (city)) {
      this.address = `${country}, ${city}`;
    } else {
      this.address = `–ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö <br> [${this.coords.latitude.toFixed(5)}, ${this.coords.longitude.toFixed(5)}]`;
    }
  }
}
