const emoji = 'üòÄ üòÅ üòÇ üòÉ üòÑ ü§£ üòÖ üòÜ üòá üòâ üòä üôÇ üôÉ üòã üòå üòç ü•∞ üòò üòó üòô üòö ü§™ üòú üòù üòõ ü§ë üò∂ üò° üò± üò® üò∞ üò¢ üò• üëç üëé'.split(' ');

if (document) {
  const btnEmoji = document.getElementById('emoji-btn'); // –ö–Ω–æ–ø–∫–∞ —Å–º–∞–π–ª–∏–∫.
  const aside = document.getElementsByTagName('aside')[0]; // –ë–æ–∫–æ–≤–∞—è –ø–∞–Ω–µ–ª—å
  const emojiContainer = document.getElementById('emoji-container'); // –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å–æ —Å–º–∞–π–ª–∏–∫–∞–º–∏

  emoji.forEach((emo) => {
    const div = document.createElement('div');
    div.innerHTML = emo;
    div.classList.add('emoji');
    div.addEventListener('click', (event) => { // –ö–ª–∏–∫ –ø–æ —Å–º–∞–π–ª–∏–∫—É
      const input = document.getElementById('input');
      if (input) input.value += event.target.innerHTML;
    });
    emojiContainer.appendChild(div);
  });

  // –ö–ª–∏–∫ –ø–æ –∫–Ω–æ–ø–∫–µ —Å–º–∞–π–ª–∏–∫.
  btnEmoji.addEventListener('click', () => {
    aside.classList.toggle('hidden');
  });
}
